import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { environment } from "environments/environment";
import { IExamNightGetVM } from "app/students/models/student-exam-night";

@Injectable({
  providedIn: "root",
})
export class ExamNightService {
  constructor(private http: HttpClient) {}

  getAllStudentExamNights(page: number, studentId: number) {
    return this.http.get<IExamNightGetVM>(
      `${environment.baseUrl}HighLighting/GetStudentHighLighting/${studentId}/${page}`
    );
  }
}
