import { UploadFileService } from "app/shared/services/General/upload-file.service";
import { NgxSpinnerService } from "ngx-spinner";
import { Component, Input, OnInit } from "@angular/core";
import { AuthService } from "app/shared/services/auth/auth.service";
import {
  SubjectModel,
  SubjectModelGetVM,
} from "app/students/models/student-subjects";
import { StudentSubjectsService } from "app/students/services/student-subjects.service";

@Component({
  selector: "app-student-subjects",
  templateUrl: "./student-subjects.component.html",
  styleUrls: ["./student-subjects.component.scss"],
})
export class StudentSubjectsComponent implements OnInit {
  mySubjects: SubjectModel[];
  @Input() userId: number;
  @Input() isParent: boolean = false;

  constructor(
    private studentSubjectsService: StudentSubjectsService,
    public authService: AuthService,
    public fileService: UploadFileService,
    private spinner: NgxSpinnerService
  ) {
    if (!this.userId) this.userId = this.authService.getUserId();
  }

  ngOnInit(): void {
    if (this.userId > 0) {
      this.spinner.show();
      this.studentSubjectsService
        .getStudentSubjects(this.userId)
        .subscribe((res: SubjectModelGetVM) => {
          this.mySubjects = res.mainSubjects;
          this.spinner.hide();
        });
    }
  }

  ngOnChanges(): void {
    console.log("userId from Subjects", this.userId);
  }

  downloadSubjectSyllabus(subjectId: number) {
    this.studentSubjectsService
      .downloadSubjectSyllabus(subjectId)
      .subscribe((res: any) => {
        console.log(res);
        this.fileService.downloadFile(res.path);
        this.spinner.hide();
      });
  }
}
