import { NgxSpinnerService } from "ngx-spinner";
import { ActivatedRoute, Router } from "@angular/router";
import { Component, Input, OnInit } from "@angular/core";
import { StudentEvaluationService } from "app/students/services/student-evaluation.service";
import { GeneralEvaluationModel } from "app/students/models/student-evaluation";
import { AuthService } from "app/shared/services/auth/auth.service";
import { GeneralService } from "app/shared/services/General/general.service";
import { IUserVM, UserModel } from "app/shared/models/general/general";

@Component({
  selector: "app-student-general-evaluation",
  templateUrl: "./student-general-evaluation.component.html",
  styleUrls: ["./student-general-evaluation.component.scss"],
})
export class StudentGeneralEvaluationComponent implements OnInit {
  @Input() isParent: boolean = false;
  GeneralEvaluation: GeneralEvaluationModel;
  percent: number;
  userId: number;
  userData: UserModel;
  constructor(
    private studentEvaluationService: StudentEvaluationService,
    public authService: AuthService,
    public generalService: GeneralService,
    private spinner: NgxSpinnerService,
    private route: ActivatedRoute
  ) {
    this.route.paramMap.subscribe((params) => {
      this.userId = Number(params.get("userId"));
      console.log("UserId Changed to", this.userId);

      if (this.userId) {
        // this.getStudentEvaluation();
        generalService.getUser(this.userId).subscribe((res: IUserVM) => {
          this.userData = res.user;
        });
      }
    });
  }

  ngOnInit(): void {
    if (!this.userId) {
      this.userId = this.authService.getUserId();
      //  if (this.userId) this.getStudentEvaluation();
    }
  }

  getStudentEvaluation() {
    this.spinner.show();
    this.studentEvaluationService
      .getGeneralEvaluation(this.userId)
      .subscribe((res: GeneralEvaluationModel) => {
        this.GeneralEvaluation = res;
        this.percent =
          (res.studentGrades / (res.examGrades != 0 ? res.examGrades : 1)) *
          100;
        this.spinner.hide();
      });
  }

  onResized(event: any) {
    setTimeout(() => {
      this.fireRefreshEventOnWindow();
    }, 300);
  }

  fireRefreshEventOnWindow = function () {
    var evt = document.createEvent("HTMLEvents");
    evt.initEvent("resize", true, false);
    window.dispatchEvent(evt);
  };
}
