import { Component, OnInit, ViewChild } from "@angular/core";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { ActivatedRoute } from "@angular/router";
import { TranslateService } from "@ngx-translate/core";
import { NgxSpinnerService } from "ngx-spinner";
import { AuthService } from "app/shared/services/auth/auth.service";
import { ExamNightService } from "app/students/services/student-exam-night.service";
import { IExamNightVM } from "app/students/models/student-exam-night";

@Component({
  selector: "app-student-examNight",
  templateUrl: "./student-exam-night.component.html",
  styleUrls: ["./student-exam-night.component.scss"],
})
export class StudentExamNightComponent implements OnInit {
  StudentExamNights: IExamNightVM[] = [];
  studentId: number = 0;
  itemsCounts: number;
  currentLang: string;
  EduCompId: number;
  functionId: number;

  // table
  displayedColumns: string[] = ["#", "subject_name", "download"];
  dataSource: MatTableDataSource<IExamNightVM>;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(
    private ExamNightServ: ExamNightService,
    private translate: TranslateService,
    private spinner: NgxSpinnerService,
    private route: ActivatedRoute,
    public authserv: AuthService
  ) {
    //use after auth for function id and row functions
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      // console.log("functionId", this.functionId);
    });

    // current language
    this.translate.onLangChange.subscribe((lang) => {
      this.currentLang = lang.lang;
    });

    this.EduCompId = Number(authserv.getEduCompId());
  }

  ngOnInit(): void {
    this.getAllStudentExamNights(1);
  }

  ngAfterViewInit() {
    if (this.dataSource) {
      this.dataSource.paginator = this.paginator;
      this.startSortingTable();
    }

    this.authserv.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.getAllStudentExamNights(1);
    });
    // for test only
    this.dataSource = new MatTableDataSource(this.StudentExamNights);
  }

  startSortingTable() {
    // custom sort
    this.dataSource.sortingDataAccessor = (item, property) => {
      switch (property) {
        case "subject_name":
          return this.currentLang == "ar"
            ? item.subject_ar_name ?? item.subject_en_name
            : item.subject_en_name ?? item.subject_ar_name;
        default:
          return item[property];
      }
    };
    this.dataSource.sort = this.sort;
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  // get all StudentExamNights
  getAllStudentExamNights(page: number) {
    this.spinner.show();
    this.ExamNightServ.getAllStudentExamNights(page, this.studentId).subscribe(
      (res) => {
        if (res) {
          // this.StudentExamNights = res.examNightsList;
          this.dataSource = new MatTableDataSource(res.examNightsList);
          this.itemsCounts = res.countItems;
          this.startSortingTable();
        }
        this.spinner.hide();
      }
    );
  }
}
