import { ActivatedRoute, Router } from "@angular/router";
import { NgxSpinnerService } from "ngx-spinner";
import { Component, OnInit } from "@angular/core";
import { TranslateService } from "@ngx-translate/core";
import { GroupByService } from "app/shared/services/General/group-by.service";
import { StudentChatService } from "app/students/services/student-chats.service";
import {
  SchedulingListVM,
  StudentScheduledChatModel,
} from "app/students/models/student-chats";
import { MessagesService } from "app/shared/services/messages.service";
import { AuthService } from "app/shared/services/auth/auth.service";
import { SweetAlertService } from "app/shared/services/sweet-alert.service";
import { SweetAlertIcon } from "app/shared/data/sweet-alert";

@Component({
  selector: "app-student-chat-time-table",
  templateUrl: "./student-chat-time-table.component.html",
  styleUrls: ["./student-chat-time-table.component.scss"],
})
export class StudentChatTimeTableComponent implements OnInit {
  //timeTable: StudentScheduledChatModel[];
  timeTable: any = [
    {
      Day: "Sunday",
      List: [
        {
          classId: 1,
          Day: "Sunday",
          DayId: 2,
          EducationalComponentUserId: 11,
          FromHour: "6:11",
          id: 63,
          messageThread: {
            Id: 31,
            TeacherSubjectId: 893,
          },
          isActive: true,
          teacherName: "Mohamed Sayed ",
          teacherPicture: "nnn",
          ToHour: "10:11",
        },
      ],
    },
    {
      Day: "Friday",
      List: [
        {
          classId: 1,
          Day: "Friday",
          DayId: 2,
          EducationalComponentUserId: 11,
          FromHour: "8:11",
          id: 1,
          messageThread: "hbjm",
          teacherName: "Mohamed Sayed ",
          teacherPicture: "nnn",
          ToHour: "12:11",
        },
        {
          classId: 1,
          Day: "Friday",
          DayId: 2,
          EducationalComponentUserId: 11,
          FromHour: "12:11",
          id: 1,
          messageThread: "hbjm",
          teacherName: "Mohamed Sayed ",
          teacherPicture: "nnn",
          ToHour: "16:11",
        },
        {
          classId: 1,
          Day: "Monday",
          DayId: 2,
          EducationalComponentUserId: 11,
          FromHour: "9:11",
          id: 1,
          messageThread: "hbjm",
          teacherName: "Mohamed Sayed ",
          teacherPicture: "nnn",
          ToHour: "12:11",
        },
      ],
    },
    {
      Day: "Monday",
      List: [
        {
          classId: 1,
          Day: "Monday",
          DayId: 2,
          EducationalComponentUserId: 11,
          FromHour: "9:11",
          id: 1,
          messageThread: "hbjm",
          teacherName: "Mohamed Sayed ",
          teacherPicture: "nnn",
          ToHour: "12:11",
        },
        {
          classId: 1,
          Day: "Monday",
          DayId: 2,
          EducationalComponentUserId: 11,
          FromHour: "9:11",
          id: 1,
          messageThread: "hbjm",
          teacherName: "Mohamed Sayed ",
          teacherPicture: "nnn",
          ToHour: "12:11",
        },
      ],
    },
  ];

  currentLang: string;
  EduCompId: number;
  functionId: number;

  constructor(
    private studentChatService: StudentChatService,
    private GroupByService: GroupByService,
    private translate: TranslateService,
    private spinner: NgxSpinnerService,
    private route: ActivatedRoute,
    private router: Router,
    private toastr: MessagesService,
    private sweetAlertService: SweetAlertService,
    public authserv: AuthService
  ) {
    //use after auth for function id and row functions
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      // console.log("functionId", this.functionId);
    });

    // current language
    this.translate.onLangChange.subscribe((lang) => {
      this.currentLang = lang.lang;
    });

    this.EduCompId = Number(authserv.getEduCompId());
  }

  ngOnInit(): void {
    this.getStudentScheduling();
  }

  ngAfterViewInit() {
    this.authserv.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      //  this.getStudentScheduling();
    });
  }

  getStudentScheduling() {
    this.spinner.show();
    this.studentChatService.getStudentScheduling().subscribe((res: any) => {
      if (res) {
        // console.log(res, this.GroupByService.groupBy(res, "Day"));
        this.timeTable = this.GroupByService.groupBy(res, "Day");
      }
      this.spinner.hide();
    });
  }

  openThread(schedule: SchedulingListVM) {
    if (schedule.messageThread?.Id != null && schedule.isActive) {
      this.router.navigate([`student/chats/${schedule.messageThread?.Id}`], {
        queryParams: {
          teacherSubjectId: schedule.messageThread?.TeacherSubjectId,
          SchedulId: schedule.id,
        },
      });
    } else
      this.sweetAlertService.SweetAlert({
        title: "labels.ChatNotAllowed",
        icon: SweetAlertIcon.info,
        describtion: "",
      });

    //this.toastr.info("labels.ChatNotAllowed");
  }
}
