import { TranslateService } from "@ngx-translate/core";
import { NgxSpinnerService } from "ngx-spinner";
import { ActivatedRoute, Router } from "@angular/router";
import { Component, OnInit } from "@angular/core";
import { AuthService } from "app/shared/services/auth/auth.service";
import { SubjectEvaluationModel } from "app/students/models/student-subjects";
import { StudentSubjectsService } from "app/students/services/student-subjects.service";
import { CourseService } from "app/dashboard/services/setup/educational-setup/course.service";
import { CourseGetVM } from "app/dashboard/models/setup/educational-setup/course";
import { GeneralService } from "app/shared/services/General/general.service";
import { IUserVM, UserModel } from "app/shared/models/general/general";

@Component({
  selector: "app-subject-evaluation",
  templateUrl: "./subject-evaluation.component.html",
  styleUrls: ["./subject-evaluation.component.scss"],
})
export class SubjectEvaluationComponent implements OnInit {
  Evaluation: SubjectEvaluationModel;
  userId: number;
  subjectId: number;
  currentLang: string;
  isParent: boolean = false;
  course: CourseGetVM;
  userData: UserModel;

  constructor(
    private studentSubjectsService: StudentSubjectsService,
    private route: ActivatedRoute,
    private router: Router,
    private spinner: NgxSpinnerService,
    public authService: AuthService,
    private courseServ: CourseService,
    private generalService: GeneralService,
    private translate: TranslateService
  ) {
    this.route.paramMap.subscribe((params) => {
      this.subjectId = Number(params.get("subjectId"));
      this.userId = Number(params.get("userId")); // get userId if parent
      if (this.userId) {
        this.isParent = true;
        generalService.getUser(this.userId).subscribe((res: IUserVM) => {
          this.userData = res.user;
        });
      }
    });

    // get userId if student
    if (!this.userId) this.userId = this.authService.getUserId();

    // current language
    this.translate.onLangChange.subscribe((lang) => {
      this.currentLang = lang.lang;
    });

    courseServ.getCourseById(this.subjectId).subscribe((res) => {
      this.course = res[0];
    });
  }

  ngOnInit(): void {
    if (this.userId > 0) {
      this.spinner.show();
      this.studentSubjectsService
        .getSubjectEvaluation(this.subjectId, this.userId)
        .subscribe((res: SubjectEvaluationModel) => {
          this.Evaluation = res;
          this.spinner.hide();
        });
    }
  }

  back() {
    if (this.isParent)
      this.router.navigateByUrl(`/parent/child-evaluation/${this.userId}`);
    else this.router.navigateByUrl("/student/mySpace");
  }

  onResized(event: any) {
    setTimeout(() => {
      this.fireRefreshEventOnWindow();
    }, 300);
  }

  fireRefreshEventOnWindow = function () {
    var evt = document.createEvent("HTMLEvents");
    evt.initEvent("resize", true, false);
    window.dispatchEvent(evt);
  };
}
