import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { StudentClassTableComponent } from "./pages/student-class-table/student-class-table.component";
import { StudentGeneralEvaluationComponent } from "./pages/student-general-evaluation/student-general-evaluation.component";
import { StudentExamNightComponent } from "./pages/student-exam-night/student-exam-night.component";
import { SubjectEvaluationComponent } from "./pages/subjects/subject-evaluation/subject-evaluation.component";
import { StudentSubjectStructureComponent } from "./pages/subjects/student-subject-structure/student-subject-structure.component";
import { StudentChatTimeTableComponent } from "./pages/chats/student-chat-time-table/student-chat-time-table.component";
import { StudentChatComponent } from "./pages/chats/student-chat/student-chat.component";

const routes: Routes = [
  { path: "", redirectTo: "mySpace", pathMatch: "full" },

  {
    path: "mySpace",
    component: StudentGeneralEvaluationComponent,
  },

  {
    path: "weekly-table",
    component: StudentClassTableComponent,
  },

  {
    path: "exam-night",
    component: StudentExamNightComponent,
  },

  {
    path: "subject-evaluation/:subjectId",
    component: SubjectEvaluationComponent,
  },

  {
    path: "subject-structure/:subjectId",
    component: StudentSubjectStructureComponent,
  },
  {
    path: "scheduled-chat",
    component: StudentChatTimeTableComponent,
  },
  {
    path: "chats/:ThreadId",
    component: StudentChatComponent,
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class StudentsRoutingModule {}
