import { NgxSpinnerService } from "ngx-spinner";
import { Component, OnInit } from "@angular/core";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { ActivatedRoute } from "@angular/router";
import { GeneralService } from "app/shared/services/General/general.service";
import { ToastrService } from "ngx-toastr";
import { AuthService } from "app/shared/services/auth/auth.service";
import {
  MsgsVM,
  MsgReplayVM,
  INewMsgVM,
  IReplayToMsgVM,
  MessageThreadModel,
  messageThreadVM,
} from "app/students/models/student-chats";
import { StudentChatService } from "app/students/services/student-chats.service";
import { UploadFileService } from "app/shared/services/General/upload-file.service";
import { IfileVM, IUserVM, UserModel } from "app/shared/models/general/general";
import { IUserModel } from "app/dashboard/models/users/user";

@Component({
  selector: "app-student-chat",
  templateUrl: "./student-chat.component.html",
  styleUrls: ["./student-chat.component.scss"],
})
export class StudentChatComponent implements OnInit {
  MsgReplaies: MsgReplayVM[]; //List of replaies For Msg
  TeacherSubjectId: number;
  currentUserId: number;
  attachecFilePath = null;

  myForm: FormGroup;
  newMsgForm: FormGroup;
  selectedMsgToReplay: MsgsVM = null;
  currentMsgUserData: MsgsVM;
  msgGroup?: messageThreadVM = null;
  isActive?: boolean = false;
  isStudent: boolean = false;
  isContentOverlay = false;
  currentThreadId: number;
  SchedulId: number;

  constructor(
    private fb: FormBuilder,
    private authService: AuthService,
    private studentChatService: StudentChatService,
    private route: ActivatedRoute,
    private GeneralService: GeneralService,
    private toastr: ToastrService,
    public uploadFileService: UploadFileService,
    private spinner: NgxSpinnerService
  ) {
    this.route.paramMap.subscribe((params) => {
      this.currentThreadId = Number(params.get("ThreadId"));
      console.log("currentThreadId", this.currentThreadId);
    });

    this.route.queryParamMap.subscribe((params) => {
      this.TeacherSubjectId = Number(params.get("teacherSubjectId"));
      this.SchedulId = Number(params.get("SchedulId"));
      console.log("TeacherSubjectId", this.TeacherSubjectId);
      console.log("SchedulId", this.SchedulId);
    });

    this.currentUserId = authService.getUserId();
  }

  ngOnInit(): void {
    this.isStudent = this.authService.isStudent();
    //  this.askTeacher(this.TeacherSubjectId);
    this.getMsgReplaies(this.currentThreadId);
    this.getCurrntThread();

    this.myForm = this.fb.group({
      threadId: [null, Validators.required],
      TeacherSubjectIds: [this.TeacherSubjectId, Validators.required],
      ParentReplyId: [null],
      Attach: [null],
      ReplyText: [null],
    });

    this.newMsgForm = this.fb.group({
      ThreadText: ["", Validators.required],
      ReplyText: ["", Validators.required],
      TeacherSubjectIds: [this.TeacherSubjectId, Validators.required],
      Attach: [null],
    });

    this.FormCtrl.threadId.setValue(this.currentThreadId);
    this.FormCtrl.TeacherSubjectIds.setValue(this.TeacherSubjectId);
    this.newMsgFormCtrl.TeacherSubjectIds.setValue(this.TeacherSubjectId);
  }

  get FormCtrl() {
    return this.myForm.controls;
  }
  get newMsgFormCtrl() {
    return this.newMsgForm.controls;
  }

  getCurrntThread() {
    this.studentChatService
      .getCurrntThread(this.currentThreadId, this.SchedulId)
      .subscribe((res: MessageThreadModel) => {
        console.log("CurrntThread", res);
        this.msgGroup = res.messageThread;
        this.isActive = res.isActive;
      });
  }

  getMsgReplaies(msgId: number) {
    this.spinner.show();
    this.studentChatService
      .getMsgReplaies(msgId)
      .subscribe((res: MsgReplayVM[]) => {
        this.MsgReplaies = res;
        // setTimeout(function () {
        //   $(".chat-box .chat-body").animate(
        //     { scrollTop: $(".chat-box .chat-body").prop("scrollHeight") },
        //     1000
        //   );
        // }, 1000);
        this.spinner.hide();
      });
  }

  ReplayToMessage() {
    this.spinner.show();
    let body = {} as IReplayToMsgVM;
    body.threadId = this.FormCtrl.threadId.value;
    body.TeacherSubjectIds = this.FormCtrl.TeacherSubjectIds.value;
    body.ParentReplyId = this.FormCtrl.ParentReplyId.value;
    body.Attach = this.FormCtrl.Attach.value;
    body.ReplyText = this.FormCtrl.ReplyText.value;

    console.log(body);
    console.log("ReplyText", this.FormCtrl.ReplyText.value);

    if (this.myForm.valid) {
      this.studentChatService.SendStudentMessage(body).subscribe((res: any) => {
        console.log("ReplayTOMessage", res);
        if (res.returnString == "Success") {
          // this.getOpenedMsgs(1, this.TeacherSubjectId);
          this.getMsgReplaies(this.FormCtrl.threadId.value);
          this.FormCtrl.ReplyText.reset();
          this.FormCtrl.Attach.reset();
          this.closePreview();
          // $(".chat-box .chat-body").animate(
          //   { scrollTop: $(".chat-box .chat-body").prop("scrollHeight") },
          //   1000
          // );
        } else {
          this.toastr.error(res.returnString);
        }
        this.spinner.hide();
      });
    }
  }

  getParentReplyId(msg: MsgsVM) {
    this.FormCtrl.ParentReplyId.setValue(msg.Id);
    this.selectedMsgToReplay = msg;
    if (msg) {
      console.log("ParentReplyIdMsg", msg);
    }
    console.log(this.myForm.value);
  }

  closePreview() {
    this.FormCtrl.Attach.setValue(null);
    this.FormCtrl.ParentReplyId.setValue("");
    this.selectedMsgToReplay = null;
    this.attachecFilePath = null;
  }

  // attache Replay Pic
  attacheReplayPic(event: any) {
    this.uploadFileService.uploadFile(event).then((file: IfileVM) => {
      setTimeout(() => {
        this.FormCtrl.Attach.setValue(file);
        this.attachecFilePath = file;
        console.log(this.attachecFilePath);
      }, 100);
    });
  }

  onLikeMsg(msg: MsgReplayVM, index: number) {
    if (!msg.isLikedByYou) this.likeMsg(msg, index);
    else this.unLikeMsg(msg, index);
  }

  likeMsg(msg: MsgReplayVM, index: number) {
    // let id = msg.Id;
    this.studentChatService
      .likeMsg({ replyId: msg.Id })
      .subscribe((res: any) => {
        this.MsgReplaies[index].isLikedByYou =
          !this.MsgReplaies[index].isLikedByYou;
        ++this.MsgReplaies[index].Likes;
        // if ((res.replyId = replyId)) {
        //   for (const obj of this.MsgReplaies) {
        //     if (obj.Id === replyId) {
        //       obj.isLikedByYou = !obj.isLikedByYou;
        //       obj.Likes = obj.Likes + 1;
        //       break;
        //     }
        //   }
        // }
      });
  }

  unLikeMsg(msg: MsgReplayVM, index: number) {
    this.studentChatService.unLikeMsg(msg.Id).subscribe((res: any) => {
      this.MsgReplaies[index].isLikedByYou =
        !this.MsgReplaies[index].isLikedByYou;
      --this.MsgReplaies[index].Likes;
    });
  }

  onSidebarToggle() {
    this.isContentOverlay = true;
  }

  onContentOverlay() {
    this.isContentOverlay = false;
  }
}
