import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { environment } from "environments/environment";

@Injectable({
  providedIn: "root",
})
export class StudentSubjectsService {
  constructor(private http: HttpClient) {}

  getStudentSubjects(userId: number) {
    return this.http.get(`${environment.baseUrl}AJAX/subjectsIndex/${userId}`);
  }

  getSubjectEvaluation(subjectId: number, userId: number) {
    return this.http.get(
      `${environment.baseUrl}AJAX/subjectEvaluation/${subjectId}/${userId}`
    );
  }

  downloadSubjectSyllabus(subjectId: number) {
    return this.http.get(
      `${environment.baseUrl}Dashboard/download-subject-syllabus/${subjectId}`
    );
  }
}
